/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-create-block-carousel {
	background-color: transparent;
	color: inherit;
	padding: 0;
}

.banner {
	width: 100%;
	box-shadow: 0 4px 32px rgb(255, 255, 255);
	background: url(./assets/bgHome.png) center top / cover;
	display: flex;
	justify-content: center;
	height: 90vh;
}

.container {
	width: 85%;
}

.wrap {
	overflow: hidden;
	display: flex;
	height: 80%;
	position: relative;
}


.bannerName {
	text-align: center;
	color: #878787;
	font-size: 30px;
	font-weight: 300;
	margin: 0 auto;
	padding: 30px 0;
	background: #fff;
}

.bannerName>span {
	color: #6AC3FF;
	text-decoration: underline #6AC3FF;
}

.btn {
	background: #18A0FB;
	text-align: center;
	border: 2px solid #18A0FB;
	box-shadow: none;
	width: 225px;
	display: block;
	padding: 14px 11px;
	font-size: 18px;
	font-weight: bold;
	letter-spacing: 0.05em;
	border-radius: 55px;
	text-decoration: none;

	&:hover {
		background: transparent !important;
		background-color: transparent !important;
		color: #18A0FB !important;
		border-color: #18a0fb !important;
	}
}

.item {
	display: flex;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;

	transition: opacity 0.5s ease-in-out;
}

.item.active {
	opacity: 1;
	z-index: 13;
}

.col6 {
	width: 50%;
	padding: 15px;
	display: flex;
	flex-direction: column;
}

.textBlock {
	margin-bottom: 59px;
	margin-left: 30px;
	min-height: 71%;
}

.title {
	//font-family: SF UI Display;
	font-size: 61px;
	font-weight: 800;
	line-height: 71px;
	letter-spacing: 0.30000001192092896px;
	text-align: left;
	color: #10233E;
}

.text {
	//font-family: Lato;
	font-size: 24px;
	font-weight: 400;
	line-height: 30px;
	letter-spacing: 0.30000001192092896px;
	text-align: left;
	color: #10233E;
}

.ing {
	display: block;
	max-width: 100%;
	height: auto;
}

.indicatorList {
	position: absolute;
	width: 400px;
	height: 20px;
	display: flex;
	bottom: 50px;
}

.carouselIndicator {
	width: 70px;
	height: 3px;
	border-radius: 20px;
	background: #C4C4C4;
	margin: 0 5px;
	cursor: pointer;
	/* content: ''; */
	display: block;
}

.carouselIndicator.active {
	background: #18A0FB;
}

//media query
@media screen and (max-width: 1300px) {
	.container {
		padding: 3% 7%;
	}

	.filterWrap {
		flex-wrap: wrap;
	}
}